.scene__container {
  background-image: url(scene.svg);
  background-size: 100vw;
  background-position: center;
  transition: background-size 3s ease-in;

  &.play {
    background-size: 400vw;
  }
}

.background__container {
  background-size: cover;
  background-position: center;
}

.screen__container {
  background-image: url(slice_background-top.svg), url(slice_background-bottom.svg);
  background-position: top, bottom;
  background-size: 100vw, 100vw;
  background-repeat: no-repeat;
  position: relative;

  h2 {
    position: absolute;
    width: 100%;
    top: 30px;
    color: #fff;
  }

  h3 {
    position: absolute;
    width: 100%;
    top: 70px;
    color: #fff;
  }

  .controll__container {
    width: 100%;
    position: absolute;
    bottom: 25px;
    height: 100px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    .charator {
      max-width: 60px;
      max-height: 60px;
    }
  }

  .question__container {
    background-color: rgba(0,0,0,0.66);
    position: absolute;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .question__dialog {
      background-image: url(slice_dialog.svg);
      font-weight: bold;
      background-size: contain;
      background-position: center;
      width: 270px;
      background-repeat: no-repeat;
      height: 160px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 20px;
      margin: 20px;
      font-size: 20px;
      padding: 0 20px;
    }

    a {
      width: 270px;
      margin: 10px;
      padding: 10px;
      border-radius: 15px;
    }
  }
}